<script>
import ChooseTransport from './detailed/ChooseTransport'
import ChooseMuseum from './detailed/ChooseMuseum'
import ChooseHotel from './detailed/ChooseHotel'
import ChooseMeal from './detailed/ChooseMeal'
import ChooseGuide from './detailed/ChooseGuide'
import ChooseAttendant from './detailed/ChooseAttendant'
import ChooseServices from './detailed/ChooseServices'

import Calculate from './detailed/Calculate'
export default {
  name: 'DetailedTour',
  components: {
    ChooseTransport,
    ChooseMuseum,
    ChooseHotel,
    ChooseMeal,
    ChooseGuide,
    ChooseAttendant,
    ChooseServices,
    Calculate,
  },
  props: {
    token: {
      type: String,
      default: '',
    },
    tourId: {
      type: String,
      default: '',
    },
    method: {
      type: String,
      default: '',
    },
    route: {
      type: String,
      default: '',
    },
  },
  data: () => {
    return {
      show: {
        transport: true,
        museum: false,
        hotel: false,
        meal: false,
        guide: false,
        attendant: false,
        services: false,
        calculate: false,
      },
    }
  },
  methods: {
    changeStep(stepName) {
      for (const key in this.show) {
        this.show[key] = false
      }
      this.show[stepName] = true
    },
  },
}
